
.button-container {
    position: absolute;
    top: 90px;
    left: 200px;
    width: 740px;
}

button {
    margin: 5px;
    border: none;
    font-size: 20px;
    font-family: 'Courier New';
}

button:focus {
    outline: none;
}

button:hover {
    color: white;
    cursor: pointer;
}

.element {
    display: inline-block;
    width: 70px;
    height: 70px;
    margin: 2px;
    border: 2px solid black;
    font-family: 'Courier New';
    float: left;
    overflow: hidden;
}

.blank-card {
    display: inline-block;
    width: 70px;
    height: 70px;
    margin: 2px;
    float: left;
    border: 2px solid black;
}


.symbol {
    position: relative;
    top: 8px;
    font-size: 33px;
    margin-left: 5px;
}

.atomic-number {
    font-size: 15px;
    position: relative;
    text-align: right;
    margin-right: 3px;
    top: -33px;
}

.name {
    position: relative;
    font-size: 10px;
    top: -12px;
    left: 6px;
}

.placeholder {
    font-size: 10px;
    line-height: 70px;
    vertical-align: middle;
    text-align: center;
}

.atomic-mass {
    position: relative;
    font-size: 11px;
    top: -12px;
    left: 5px;
}

.gap-1 {
    width: 1248px;
    float: left;
}

.gap-2 {
    width: 780px;
    float: left;
}

.gap-3 {
    width: 1200px;
    height: 40px;
    float: left
}

.gap-4 {
    width: 234px;
    float: left;
}

